  position: relative;
  font-size: 16px;
}

blockquote cite:after {
  position: absolute;
  right: -50px;
  top: 0;
  width: 40px;
  height: 2px;
  background: #FFA031;
  content: '';
  bottom: 0;
  margin: auto;
}

